.bgloading{
    background: rgba(51, 51, 51, 0.644);
    position: fixed;
    top:0px; 
    left:0px ;
    width:100% ;
    height: 100% ;
    &__image{
     position: absolute;
     top:50% ;
     left:50% ;
     transform: translate(-50% , -50%);
     width:100px; 
     height: 100px;
    }
}
